/* Navigation */
.navigation{
  position: relative;
  height: 7rem;
  box-shadow: 0 .5rem 1.5rem rgba(0, 0, 0, .1);
}

.nav{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 7rem;
  width: 100%;
  padding: 0.1rem;
}

.nav__logo a{
  font-size: 2.5rem;
  color: #222;
  color: var(--black);
  padding: 1.6rem;
  font-weight: 700;
}

.nav__hamburger {
  display: none;
  cursor: pointer;
}

.nav__hamburger i{
  height: 2.3rem;
  width: 2.3rem;
}

.menu__top {
  display: none;
}

.nav__menu {
  width: 50%;
}

.nav__list{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 50%;
}

.nav__item:not(:last-child) {
  margin-right: 1.6rem;
}

.nav__list .nav__link:link,
.nav__list .nav__link:visited{
  display: inline-block;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: #222;
  color: var(--black);
  transition: color .3s ease-in-out;
}

.nav__list .nav__link:hover{
  color: #eb0028;
  color: var(--orange);
}


/* Navigation */
@media only screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: 0;
    left: -30rem;
    width: 0;
    background-color: #fff;
    background-color: var(--white);
    z-index: 9990;
    height: 100%;
    transition: all .3s;
  }

  .nav__menu.open{
    left: 0rem;
    width: 30rem;
  }

  .nav__logo {
    width: 50%;
  }

  .nav__hamburger {
    display: block;
  }

  .menu__top{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: #eb0028;
    background-color: var(--orange);
    padding: 1.8rem 1rem;
  }

  .menu__top i{
    height: 1.6rem;
    width: 1.6rem;
    fill: #f1f1f1;
    fill: var(--primaryColor);
  }

  .nav__category{
    color: #fff;
    color: var(--white);
    font-size: 2.3rem;
    font-weight: 700;
  }

  .nav__list{
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 2.6rem 1rem;
  }

  .nav__item:not(:last-child) {
    margin-right: 0;
  }

  .nav__item {
    width: 100%;
    margin: 1.4rem 0;
  }

  .nav__list .nav__link:link,
  .nav__list .nav__link:visited {
    padding: 1.6rem 0;
    width: 100%;
    color: #666;
    color: var(--grey2);
  }

  body.active::before{
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #222 none no-repeat 0 0;
    background: var(--black) none no-repeat 0 0;
    opacity: .7;
    z-index: 999;
    transition: .8s;
  }
}

@media only screen and (max-width: 568px){
  .nav__icons .icon_item i{
    width: 1.5rem;
    height: 1.5rem;
  }

  .nav__icons .icon__item{
    padding: .5rem;
    
  }
}

/* hero styling */

