/* pop-up */
.popup {
  position: fixed;

  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  transition: 0.3s;
  -webkit-transform: scale(1);
          transform: scale(1);
  overflow-y: scroll;
}

.popup__content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70%;
  margin: 0 auto;
  height: 60rem;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 1.6rem;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  overflow: hidden;
  text-align: center;
  color: white;
  font-family: 'Old Standard TT';
}


