:root {
    --main-color: #2980b9;
    --form-bg: rgba(0, 0, 0, 0.8);
    --border-color: #2980b9;
    --form-color: #c6c6c6;
    --form-input-bg: rgba(2, 2, 2, 0.22);

    --border-radius: 10px;
    --form-logo-size: 200px;
    --form-max-width: 500px;
}
/* 
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
} */

.body {
    font-family: "Source Sans Pro", sans-serif;
    height: 100vh;
    position: relative;
    display: grid;
    place-items: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    overflow-y: scroll;
}

.form {
    padding: 50px 60px;
    -webkit-justify-content: center;
            justify-content: center;
    grid-gap: 2rem;
    background-color: rgba(0, 0, 0, 0.8);
    background-color: var(--form-bg);
    border-radius: 10px;
    border-radius: var(--border-radius);
    color: #c6c6c6;
    color: var(--form-color);
    text-align: center;
    width: 100%;
    max-width: 500px;
    max-width: var(--form-max-width);
}

.form-logo h2{
    color: #ff4545;
    font-size: 2.7rem;
    font-weight: 500;
    padding: 0.5rem;
    margin-top: -4rem;
    margin-bottom: 2rem;
    font-family: 'Lobster';
    text-shadow: 0.5rem 0.5rem 1rem #b8a1a1;
}

.form-logo img {
    width: 200px;
    width: var(--form-logo-size);
}

.form-group {
    position: relative;
    margin-bottom: 20px;
}

.form-input {
    background-color: rgba(2, 2, 2, 0.22);
    background-color: var(--form-input-bg);
    border: 2px solid transparent;
    height: 46px;
    width: 100%;
    position: relative;
    color: #c6c6c6;
    color: var(--form-color);
    border-radius: 10px;
    border-radius: var(--border-radius);
    padding: 0 20px;
    font-size: 16px;
    outline: none;
    transition: border 0.3s ease-in-out;
}

.form-input::-webkit-input-placeholder {
    color: #c6c6c6;
    color: var(--form-color);
}

.form-input:-ms-input-placeholder {
    color: #c6c6c6;
    color: var(--form-color);
}

.form-input::placeholder {
    color: #c6c6c6;
    color: var(--form-color);
}

.form-input:focus {
    border: 2px solid #2980b9;
    border: 2px solid var(--border-color);
}

.form-input-icon {
    position: absolute;
    right: 10px;
    top: 14px;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.form-input-icon.err {
    color: #c0392b;
}

.form-input-icon.success {
    color: #27ae60;
}

.form-group.err .form-input-icon.err,
.form-group.success .form-input-icon.success {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.form-input-err-msg {
    display: block;
    text-align: left;
    font-size: 14px;
    margin-top: 10px;
    padding-left: 20px;
    color: #c0392b;
}

.form-group-checkbox {
    text-align: left;
}

.form-group-checkbox input {
    opacity: 0;
    height: 0;
    width: 0;
    cursor: pointer;
}

.form-group-checkbox label {
    cursor: pointer;
    padding-left: 30px;
    position: relative;
}

.form-group-checkbox label::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 22px;
    height: 22px;
    background-color: rgba(2, 2, 2, 0.22);
    background-color: var(--form-input-bg);
}

.form-group-checkbox label i {
    position: absolute;
    left: 2px;
    top: 2px;
    color: #2980b9;
    color: var(--main-color);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    -webkit-transform: scale(0);
            transform: scale(0);
}

.form-group-checkbox input:checked + label i {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.form-btn {
    margin: 15px 0;
    height: 46px;
    width: 100%;
    border-radius: 10px;
    border-radius: var(--border-radius);
    border: 2px solid #2980b9;
