:root {
    --main: orange;
    --bg: #0B1218;
    --bg-body: #131D28;
}

input, button{
    outline: none;
}

#menu-toggle {
    display: none;
}

#menu-toggle:checked ~ .sidebar .side-img {
    height: 50px;
    width: 50px;
    margin-bottom: 1rem;
}

#menu-toggle:checked ~ .sidebar h2 {
    display: none;
}

#menu-toggle:checked ~ .sidebar .user{
    display: none;
}

#menu-toggle:checked ~ .sidebar li a span:last-child{
    display: none;
}

#menu-toggle:checked ~ .sidebar {
    width: 65px;
}

#menu-toggle:checked ~ .main-content .header {
    left: calc(65px + 1rem);
}

#menu-toggle:checked ~ .main-content {
    margin-left: 65px;
}

.sidebar {
    height: 100%;
    left: 0;
    top: 0;
    position: fixed;
    background: #0B1218;
    background: var(--bg);
    width: 280px;
    overflow-y: auto;
    transition: width 300ms;

}

.brand {
    padding: .8rem 1rem;
    display: -webkit-flex;
    display: flex;
    color: orange;
    color: var(--main);
    height: 50px;
    -webkit-justify-content: center;
            justify-content: center;
}

.brand span{
    font-size: 2rem;
    margin-top: .7rem;
    margin-right: 1rem;
}

.sidebar-user{
    margin: 3rem 0rem; 
}

.side-img {
    height: 100px;
    width: 100px;
    margin: auto;
    border: 4px solid #3e454d;
    border-radius: 50%;
    display: grid;
    place-content: center;
}

.user {
    text-align: center;
    color: #fff;
    margin-top: .2rem;
}

.user small {
    display: inline-block;
    color: orange;
    color: var(--main);
}

.sidemenu {
    padding: .5rem;
}

.sidemenu li {
    margin-bottom: 1.5rem;
}

.sidemenu a {
    color: #fff;
    font-size: 1.2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.sidemenu a span:first-child{
    font-size: 1.3rem;
    padding: .5rem .6rem;
}

.sidemenu a.active {
    background: orange;
    background: var(--main);
    color: #fff;
    border-radius: 4px;
    height: 45px;
}

.sidemenu a:hover {
    /* background: var(--main); */
    color:  orange;
    color:  var(--main);
    /* border-radius: 4px; */
    /* height: 45px; */
}

.main-content{
    margin-left: 280px;
    min-height: 100vh;
    background: #131D28;
    background: var(--bg-body);
    padding: .5rem 1rem;
    transition: margin-left 300ms;
}

.header{
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background: #0B1218;
    background: var(--bg);
    padding: 0rem 1rem;
    position: fixed;
    left: calc(280px + 1rem);
    right: 1rem;
    border-radius: 3px;
    transition: left 300ms;
    z-index: 100;
    top:-1rem;
}

.menu-toggle {
    height: 45px;
    width: 45px;
    background: orange;
    background: var(--main);
    color: #fff;
    display: grid;
    place-items: center;
    border-radius: 5px;
}

.search {
    display: -webkit-flex;
    display: flex;
    width: 300px;
    border-radius: 30px;
    overflow-x: hidden;
    border: 3px solid #3e454d;
    -webkit-align-items: center;
            align-items: center;
}

.search span {
    font-size: 1.3rem;
    color: #fff;
    padding-left: 1rem;
}

.search input{
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 42px;
    border: none;
    background: transparent;
    padding: 1rem;
    color: #fff;
}

.head-icons span{
    font-size: 1.5rem;
    margin-left: 1rem;
    color: #fff;
}

main{
    margin-top: calc(70px + .5rem);
    padding: 1rem 0rem;
}

.cardas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 3rem;
}

.carda {
    background: #0B1218;
    background: var(--bg);
    padding: 2rem 1rem;
    border-radius: 3px;
}

.carda-icon {
    text-align: center;
    display: grid;
    place-items: center;
}

.carda-icon span{
    height: 55px;
    width: 55px;
    color: #fff;
    font-size: 2rem;
    border-radius: 6px;
    display: grid;
    place-items: center;
}

.follow span {
    color: #0072f2;
    background: #e5f3fe;
}

.likes span {
    color: #f42a26;
    background: #fde9ea;
}

.comment span {
    color: #ffbc01;
    background: #fff8e5;
}

.carda-info {
    text-align: center;
    margin-top: .6rem;
}

.carda-info h2{
    font-size: 3rem;
    color: #efefef;
}

.carda-info small {
    display: inline-block;
    color: orange;
    color: var(--main);
    font-weight: 500;
}

.carda-images {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 1.3rem;
}

.carda-images img{
    height: 45px;
    width: 45px;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    border: 4px solid #efefef;
    position: relative;
    margin-left: -15px;
}

.carda-images div:first-child{
    margin-left: 0;
}

.chart-grid {
    display: grid;
    grid-template-columns: 64% auto;
    margin-top: 3rem;
    grid-gap: 2.5rem;
}

.chart-follow, .chart-sales {
    background: #0B1218;
    background: var(--bg);
    padding: 1rem;
    border-radius: 3px;
    height: auto;
}

.chart-head {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #efefef;
    padding-bottom: 1rem;
    border-bottom: 1px solid #222;
    margin-bottom: 1rem;
}

.close-mobile-menu {
    position: fixed;
    right: -100%;
    top: 0;
    bottom: 0;
    display: block;
    height: 100%;
    width: calc(100% - 280px);
    background: transparent;
    z-index: 100;
    transition: right 300ms;
}

@media only screen and (max-width: 1124px) {
    #menu-toggle ~ .sidebar .brand h2 {
        display: none;
    }
    
    #menu-toggle ~ .sidebar .side-img {
        height: 50px;
        width: 50px;
    }
    
    #menu-toggle ~ .sidebar .user{
        display: none;
    }
    
    #menu-toggle ~ .sidebar li a span:last-child{
        display: none;
    }
    
    #menu-toggle ~ .sidebar {
        width: 65px;
    }
    
    #menu-toggle ~ .main-content .header {
        left: calc(65px + 1rem);
    }
    
    #menu-toggle ~ .main-content {
        margin-left: 65px;
    }

    /* second toggle */

    #menu-toggle:checked ~ .sidebar .brand h2 {
        display: none;
    }
    
    #menu-toggle:checked ~ .sidebar .side-img {
        height: 120px;
        width: 120px;
    }
    
    #menu-toggle:checked ~ .sidebar .user{
        display: block;
    }
    
    #menu-toggle:checked ~ .sidebar li a span:last-child{
        display: inline-block;
    }
    
    #menu-toggle:checked ~ .sidebar {
        width: 280px;
        z-index: 100;
    }
    
    /* #menu-toggle:checked ~ .main-content header {
        left: calc(65px + 1rem);
    }
    
    #menu-toggle:checked ~ .main-content {
        margin-left: 65px;
    } */

    #menu-toggle:checked ~ .close-mobile-menu {
        right: 0;
    }
}

@media only screen and (max-width: 845px) {
    .cards {
        grid-template-columns: repeat(2, 1fr);
    }

    .chart-grid {
        grid-template-columns: 100%;
    }
}

@media only screen and (max-width: 768px) {
    .search {
        display: none;
    }
}

@media only screen and (max-width: 630px) {
    .cards {
        grid-template-columns: 100%;
    }
}
